<template>
  <div class="target_contents_wrap pop-scroll clearfix" v-if="isShow">
    <transition name="modal">
      <ui-dialog :dialogData="dialogData" v-if='dialogShow' @ok='dialogOk' @cancel="dialogCancel"></ui-dialog>
    </transition>
    <div class="target_contents_inner">
      <div class="target_thead">
        <div class="main_title">
          <div><img src="../../../assets/images/target/target_logo_07.png" alt="member"></div>
          <div class="title_info">
            <p></p>
            <p>  </p>
          </div>
        </div>
        <div class="use_wrap">
          <div class="use_select">
            <div class="contents_title"></div>
            <ui-select :selectData="adAccountPixels" data-key="adAccountPixels" :onClick="selectOnClick"></ui-select>
          </div>
          <div class="use_date">
            <div> : </div>
            <div><input type="text" v-model="registrationDay"><span></span></div>
          </div>
        </div>
        <div class="target_name">
          <div class="contents_title"></div>
          <div><input type="text" v-model="registrationName"></div>
        </div>
        <div class="target_data">
          <div class="contents_title"> </div>
          <div>
            <span>12,000</span>
          </div>
        </div>
      </div>
      <div class="target_tbody">
        <div class="target_inner_tbody clearfix">
          <div class="target_generate">
            <div class="account_info">
              <div class="account_title">" " </div>
              <div>
                <ui-select :selectData="this.selectRegistrationUser" data-key="selectRegistrationUser" :onClick="selectOnClick"></ui-select>
              </div>
              <div class="account_date" v-if="subSelect">
                <ui-select :selectData="this.selectSub" data-key="selectSub" :onClick="selectOnClick"></ui-select>
              </div>
              <div class="account_date" v-if="subInput">
                <input type="text" v-if="subInput"><span></span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="btn_wrap">
      <button class="before_btn close_pop" @click="tabMove(0)"></button>
      <button class="next_btn" @click="createRegistration()" v-if="makeType == 'add'"> </button>
      <button class="delete_btn" @click="createRegistrationDelete()" v-if="makeType == 'modify'"></button>
      <button class="next_btn" @click="createRegistration()" v-if="makeType == 'modify'"> </button>
    </div>
  </div>
</template>

<script>
import Select from '@/components/ui/Select'
import Dialog from '@/components/ui/Dialog'

export default {
  name: '',

  components: {
    'ui-select': Select,
    'ui-dialog':Dialog
  },

  props: {
    isShow: {
      type: Boolean,
      default () {
        return false
      }
    },
    adAccountPixels: {
      type: Object,
      default () {
        return {
          emptyText: '  .',
          textList: [
            '  .'
          ]
        }
      }
    },
    tabMove: {
      type: Function
    },
    makeType: {
      type:String
    },
    makeItem: {
      type: Object
    }
  },

  data () {
    return {
      registrationDay: '30',
      registrationName: '',

      subSelect:false,
      subInput:false,

      dialogShow:false,
      dialogData:{
        emptyText:'sample',
        type:'confirm',
        mode:'sample'
      },
      nextStage:false,

      selectRegistrationUser: {
        emptyText: ' ',
        textList: [
          ' ',
          '   ', //   (5/15/25 %)
          '  ',
          '  ',
          '  '
        ],
        keyList: [
          'total',
          'usage_time_top',
          'non_purchase',
          'conversion',
          'non_conversion'
        ]
      },
      selectSub: {
        emptyText: '5%',
        textList: [
          '5%',
          '15%',
          '25%'
        ],
        keyList: [
          '5',
          '15',
          '25'
        ]
      }
    }
  },

  methods: {
    dialogOpen(emptyText, type, mode) {
      this.dialogData['emptyText'] = emptyText
      this.dialogData['type'] = type
      this.dialogData['mode'] = mode
      this.dialogShow = true;
    },
    dialogOk() {
      const mode = this.dialogData.mode

      if(mode == 'registration') {
        // TODO
      } else if (mode === 'registrationDelete') {
        this.$emit('deleteCustomTarget', this.makeItem.id)
      }

      // 
      this.nextStage = true
      this.dialogShow = false;
    },
    dialogCancel() {
      this.nextStage = false;
      this.dialogShow = false;
    },

    selectOnClick (item) {
      const key = event.target.closest('.select_btn').getAttribute('data-key')
      const textCheck = item.replace(/\s/gi, "")
      this.subSelect = false
      this.subInput = false

      //  
      if(textCheck === '' || key === 'selectSub') {
        this.subSelect = true
      }else if(textCheck === '') {
        this.subInput = true
      }
      this[key].emptyText = item
    },

    findSelectKey (selectName) {
      /*
      Select Key 
      */
      const emptyText = this[selectName].emptyText
      const textList = this[selectName].textList
      const keyList = this[selectName].keyList
      return keyList[textList.indexOf(emptyText)]
    },

    createRegistration () {
      let params = {
        fb_ad_account_id: localStorage.getItem('fb_ad_account_id'),
        target_type: 'registration',
        pixel_id: this.findSelectKey('adAccountPixels'),
        name: this.registrationName,
        retention_days: this.registrationDay,

        detail: this.findSelectKey('selectRegistrationUser'),
        input_percent: this.findSelectKey('selectSub')
      }

      this.$http.post('/pickdata_account_target/custom_target', params)
      .then((response) => {
        var success = response.data.success;
        if (success == "YES") {
          // success
          this.$eventBus.$emit('getAccountTarget')
        } else {
          //,  -  (confirm,alert) - (alert   X)
          this.dialogOpen('   ', 'alert')
          throw('success: ' + success)
        }
        this.$emit('close')
      })
      .catch(err => {
        this.$emit('close')
        console.log('/pickdata_account_target/custom_target: ', err)
      })
    },

    createRegistrationDelete () {
      this.dialogOpen('?', 'confirm', 'registrationDelete')
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



