<template>
  <div class="target_contents_wrap pop-scroll clearfix" v-if="isShow">
    <transition name="modal">
      <ui-dialog :dialogData="dialogData" v-if='dialogShow' @ok='dialogOk' @cancel="dialogCancel"></ui-dialog>
    </transition>
    <div class="target_contents_inner">
      <div class="target_thead">
        <div class="main_title">
          <div><img src="../../../assets/images/target/target_logo_01.png" alt="neo"></div>
          <div class="title_info">
            <p> </p>
            <p>  </p>
          </div>
        </div>
        <div class="use_wrap">
          <div class="use_select">
            <div class="contents_title"> </div>
            <ui-select :selectData="adAccountPixels" data-key="adAccountPixels" :onClick="selectOnClick"></ui-select>
          </div>
          <div class="use_date">
            <div>  : </div>
            <div><input type="text" v-model="collectionPeriod" onkeyup="this.value=this.value.replace(/[^0-9]/g, '')"><span></span></div>
          </div>
        </div>
        <div class="target_name">
          <div class="contents_title"> </div>
          <div><input type="text" v-model="targetName"></div>
        </div>
        <div class="target_data">
          <div class="contents_title"> </div>
          <div>
            <span>{{ this.audienceSize }}</span>
          </div>
        </div>
      </div>
      <div class="target_tbody">
        <div class="target_inner_tbody clearfix">
          <div class="target_generate">
            <div class="account_info">
              <div class="account_title">" "</div>
              <div>
                <ui-select :selectData="selectUser" data-key="selectUser" :onClick="selectOnClick"></ui-select>
              </div>
              <div class="account_date" v-if="subSelect">
                <ui-select :selectData="selectSub" data-key="selectSub" :onClick="selectOnClick"></ui-select>
              </div>
              <div class="account_date" v-if="subInput">
                <input type="text" v-if="subInput" v-model="unvisitedPeriod" onkeyup="this.value=this.value.replace(/[^0-9]/g, '')"><span></span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="btn_wrap">
      <button class="before_btn close_pop" @click="tabMove(0)"></button>
      <button class="next_btn" @click="createVisitSite()" v-if="makeType == 'add'"> </button>
      <button class="delete_btn" @click="createVisitSiteDelete()" v-if="makeType == 'modify'"></button>
      <button class="next_btn" @click="updateVisitSite()" v-if="makeType == 'modify'"> </button>
    </div>
  </div>
</template>

<script>
import { numberFormatter } from '@/components/utils/Formatter'
import Select from '@/components/ui/Select'
import Dialog from '@/components/ui/Dialog'

export default {
  name: 'VisitSite',

  components: {
    'ui-select': Select,
    'ui-dialog':Dialog
  },

  props: {
    isShow: {
      type: Boolean,
      default () {
        return false
      }
    },
    adAccountPixels: {
      type: Object,
      default () {
        return {
          emptyText: '  .',
          textList: [
            '  .'
          ]
        }
      }
    },
    tabMove: {
      type: Function
    },
    makeType: {
      type:String
    },
    makeItem: {
      type: Object
    }
  },

  created () {
    this.$eventBus.$on('modifyTarget', this.modifyTarget)
  },

  beforeDestroy () {
    this.$eventBus.$off('modifyTarget', this.modifyTarget)
  },

  data () {
    return {
      collectionPeriod: '30',
      unvisitedPeriod: '1',
      targetName: '',
      audienceSize: '-',

      subSelect: false,
      subInput: false,

      dialogShow:false,
      dialogData:{
        emptyText:'sample',
        type:'confirm',
        mode:'sample'
      },
      nextStay:false,

      selectUser: {
        emptyText: ' ',
        textList: [
          ' ',
          '   ', //   (5/15/25 %)
          '   ', //    
          '',
          ' ',
          '  ',
          ' ',
          '  ',
          ' '
        ],
        keyList: [
          'total',
          'usage_time_top', //   (5/15/25 %)
          'non_visit', //    
          'purchase',
          'non_purchase',
          'add_to_cart',
          'conversion',
          'non_conversion',
          'registration'
        ],
      },
      selectSub: {
        emptyText: '5%',
        textList: [
          '5%',
          '15%',
          '25%'
        ],
        keyList: [
          '5',
          '15',
          '25'
        ]
      }
    }
  },

  watch: {
    collectionPeriod (day) {
      if (day > 180) {
        //,  -  (confirm,alert) - (alert   X)
        this.dialogOpen('   180 .', 'alert')
        this.collectionPeriod = 180
      } else if (this.collectionPeriod === '0') {
        alert('   1.')
        this.collectionPeriod = 1
      }
    },

    unvisitedPeriod (day) {
      if(day >= this.collectionPeriod) {
        alert('    .')
        this.unvisitedPeriod = this.collectionPeriod - 1
      }
    },

    targetName (name) {
      if (name.length > 48) {
        //,  -  (confirm,alert) - (alert   X)
        this.dialogOpen('   48 .', 'alert')
        this.targetName = name.substr(0,48)
      }
    }
  },

  methods: {
    dialogOpen(emptyText, type, mode) {
      this.dialogData['emptyText'] = emptyText
      this.dialogData['type'] = type
      this.dialogData['mode'] = mode
      this.dialogShow = true;
    },
    dialogOk() {
      const mode = this.dialogData.mode

      if (mode === 'visitSite') {
        this.createVisitSiteNext()
      } else if (mode === 'visitSiteDelete') {
        this.$emit('deleteCustomTarget', this.makeItem.id)
      } else if (mode === 'visitSiteUpdate') {
        this.updateVisitSiteNext()
      }

      // 
      this.nextStay = true
      this.dialogShow = false;
    },
    dialogCancel() {
      this.nextStay = false;
      this.dialogShow = false;
    },
    selectOnClick (item) {
      const key = event.target.closest('.select_btn').getAttribute('data-key')
      const textCheck = item.replace(/\s/gi, "")
      this.subSelect = false
      this.subInput = false

      //  
      if(textCheck === '' || key === 'selectSub') {
        this.subSelect = true
      }else if(textCheck === '') {
        this.subInput = true
      }
      this[key].emptyText = item
    },

    findSelectText (selectName, key) {
      /*
      Select Text 
      */
      const textList = this[selectName].textList
      const keyList = this[selectName].keyList
      return textList[keyList.indexOf(key)]
    },

    findSelectKey (selectName) {
      /*
      Select Key 
      */
      const emptyText = this[selectName].emptyText
      const textList = this[selectName].textList
      const keyList = this[selectName].keyList
      return keyList[textList.indexOf(emptyText)]
    },

    createVisitSite () {
      if (this.collectionPeriod.length === 0) {
        //,  -  (confirm,alert) - (alert   X)
        this.dialogOpen('  .', 'alert')
      } else if (this.unvisitedPeriod.length === 0) {
        //,  -  (confirm,alert) - (alert   X)
        this.dialogOpen('  .', 'alert')
      } else if (this.targetName.length === 0) {
        //,  -  (confirm,alert) - (alert   X)
        this.dialogOpen('  .', 'alert')
      } else {
        //,  -  (confirm,alert) - (alert   X)
        this.dialogOpen('   ?', 'confirm', 'visitSite')
      }
    },

    createVisitSiteNext () {
      let params = {
        fb_ad_account_id: localStorage.getItem('fb_ad_account_id'),
        target_type: 'visit_site',
        pixel_id: this.findSelectKey('adAccountPixels'),
        name: this.targetName,
        retention_days: this.collectionPeriod,
        exclusion_retention_days: this.unvisitedPeriod,

        detail: this.findSelectKey('selectUser'),
        input_percent: this.findSelectKey('selectSub')
      }

      this.$http.post('/pickdata_account_target/custom_target', params)
      .then((response) => {
        var success = response.data.success
        if (success == "YES") {
          // success
          this.$eventBus.$emit('getAccountTarget')
        } else {
          //,  -  (confirm,alert) - (alert   X)
          this.dialogOpen('   ', 'alert')
          throw('success: ' + success)
        }
        this.$emit('close')
      })
      .catch(err => {
        this.$emit('close')
        console.log('/pickdata_account_target/custom_target: ', err)
      })
    },

    createVisitSiteDelete () {
      this.dialogOpen('?', 'confirm', 'visitSiteDelete')
    },

    updateVisitSite () {
      this.dialogOpen('?', 'confirm', 'visitSiteUpdate')
    },

    updateVisitSiteNext () {
      console.log('update call')
      let params = {
        pickdata_account_target_id: this.makeItem.id,
        fb_ad_account_id: localStorage.getItem('fb_ad_account_id'),
        target_type: 'visit_site',
        pixel_id: this.findSelectKey('adAccountPixels'),
        name: this.targetName,
        retention_days: this.collectionPeriod,
        exclusion_retention_days: this.unvisitedPeriod,

        detail: this.findSelectKey('selectUser'),
        input_percent: this.findSelectKey('selectSub')
      }

      this.$http.put('/pickdata_account_target/custom_target', params)
      .then((response) => {
        var success = response.data.success
        if (success == "YES") {
          // success
          this.$eventBus.$emit('getAccountTarget')
        } else {
          this.dialogOpen('   ', 'alert')
          throw('success: ' + success)
        }
        this.$emit('close')
      })
      .catch(err => {
        this.$emit('close')
        console.log('/pickdata_account_target/custom_target delete: ', err)
      })
    },

    // TODO Delete Function
    createVisiteSiteDeleteNext () {
      console.log('delete call')

      // this.$http.put('/pickdata_account_target/custom_target', {
      //   pickdata_account_target_id: this.makeItem.id
      // })
      // .then((response) => {
      //   console.log(response)
      // })
      // .catch(err => {
      //   this.$emit('close')
      //   console.log('/pickdata_account_target/custom_target delete: ', err)
      // })\\
      this.$http.delete('/pickdata_account_target/custom_target', {
        data: {
          pickdata_account_target_id: this.makeItem.id
        }
      })
      .then((response) => {
        const success = response.data.success
        if (success == "YES") {
          // success
          this.$eventBus.$emit('getAccountTarget')
        } else {
          //,  -  (confirm,alert) - (alert   X)
          this.dialogOpen('   ', 'alert')
          throw('success: ' + success)
        }
        this.$emit('close')
      })
      .catch(err => {
        this.$emit('close')
        console.log('/pickdata_account_target/custom_target delete: ', err)
      })
    },

    // /fb_ad_accounts/ad_account_pixels call after
    modifyTarget (pixelData) {
      console.log('# : ', this.makeItem)
      console.log('@ : ', this.adAccountPixels.emptyText)
      console.log('@ : ', this.findSelectText('adAccountPixels', this.makeItem.description.params.pixel_id))

      // Custom Target  params 
      const params = this.makeItem.description.params
      const detail = params.detail

      //  
      this.adAccountPixels.emptyText = this.findSelectText('adAccountPixels', this.makeItem.description.params.pixel_id)

      //  
      this.collectionPeriod = numberFormatter(this.makeItem.description.retention_days)

      //  
      this.targetName = this.makeItem.name

      //  
      this.audienceSize = numberFormatter(this.makeItem.display_count)

      //   @
      if (detail === 'total') {
        //  
        this.selectUser.emptyText = ' '
      } else if (detail === 'usage_time_top') {
        //    
        this.selectUser.emptyText = '   '
        this.selectSub.emptyText = params.input_percent + '%'
        this.subSelect = true
      } else if (detail === 'non_visit') {
        //    
        this.selectUser.emptyText = '   '
        this.subInput = true
      } else if (detail === 'purchase') {
        //  
        this.selectUser.emptyText = ' '
      } else if (detail === 'non_purchase') {
        //  
        this.selectUser.emptyText = ' '
      } else if (detail === 'add_to_cart') {
        //   
        this.selectUser.emptyText = '  '
      } else if (detail === 'conversion') {
        //  
        this.selectUser.emptyText = ' '
      } else if (detail === 'non_conversion') {
        //  
        this.selectUser.emptyText = ' '
      } else if (detail === 'registration') {
        //  
        this.selectUser.emptyText = ' '
      } else {
        console.log('nothing..', detail)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



